.button {
  -webkit-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}

.button.social {
  width: 40px;
  height: 40px;
  font-size: 1.2rem;
  position: relative;
  display: inline-block;
  text-align: center;
  margin-left: .25rem;
  background: none;
  border: none;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  line-height: 40px;
}

.button.social .fab,
.button.social .fas,
.button.social .far {
  background: none;
  z-index: 1;
  line-height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.button.social:hover {
  color: #FFF !important;
}

