}

#content {
  overflow: auto;
}

.grid {
  width: 100%;
}

#content {
  position: relative;
  padding-top: 55px;
}

#drawer {
  width: 300px;
  transform: translateX(-300px);
  background: #161616;
  color: white;
  font-family: Montserrat, sans-serif;
}

#drawer.is-open {
  -webkit-transform: translate(0, 0);
  -moz-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
}

footer {
  background: #101010;
  padding: 2rem 0;
  text-align: center;
  font-size: .9rem;
  color: rgba(255, 255, 255, 0.5);
}

footer .copyright {
  display: block;
  margin: 0;
}

footer .social {
  padding: 10px 0;
  font-size: 1.5rem;
}

footer .social a {
  margin: 0 10px;
}

footer .social a.gitlab {
  color: #E24329;
}

footer .social a.linkedin {
  color: #0077B5;
}

footer .social a.twitter {
  color: #55acee;
}

.tab-container {
